        <v-timeline style="margin: 20px">
            <v-timeline-item
                v-for="item in educationData"
                :key="item.key"
                :color="item.color"
                medium>

                <!-- Card content -->
                <v-card
                    class="elevation-2"
                    :href="item.link"
                    target="_blank">
                    <v-card-title 
                        class="title white" 
                        :class="item.color">
                        {{item.title}}
                    </v-card-title>

                    <v-card-text>
                        {{item.description}}
                    </v-card-text>
                </v-card>

                <!-- Date Range & logo at Opposite-->
                <p slot="opposite" :class="`headline font-weight-bold ${item.color}--text`">{{item.timeRange}}</p>
                <!-- <v-img
                    class="logo"
                    contain
                    v-if="item.logo"
                    :src="require(`@/assets/${item.logo}`)"
                    slot="opposite"
                ></v-img> -->
            </v-timeline-item>
        </v-timeline>
    </div>
</template>

<script>
import educationData from '../assets/education.json' 
export default {
    data() {
        return {
            "educationData": educationData
        }
    },
}
</script>

