<template>
    <v-tabs
      v-model="active"
      color="cyan"
      fixed-tabs
      dark
      icons-and-text
      slider-color="orange">

      <!-- Define Tabs -->
      <v-tab
        v-for="tab in tabComponents"
        :key="tab.key"
        ripple>

        {{ tab.displayName }}
        <v-icon>{{tab.icon}}</v-icon>


      </v-tab>

      <!-- Display Component -->
      <v-tab-item
        v-for="comp in tabComponents"
        :key="comp.key">
        <component :is="comp.name"></component>
      </v-tab-item>
    </v-tabs>
</template>

<script>
import Landing from './Landing'
import About from './About'
import Education from './Education'
import Contact from './Contact'
import tabData from '../assets/tabs.json'

export default {
    components: {
        Landing,
        About,
        Education,
        Contact,
  },
  data () {
    return {
        "active": null,
        "tabComponents": tabData
    }
  }
}
</script>
