<template>


    <v-form class="" target="_blank" action="https://formspree.io/pabril@student.42.fr" method="POST">
        <v-container>
            
            <v-alert
            value="true"
            class=""
            type="info">
            If you're not a form person, you can simply email me at 
                <v-btn round target="_blank" href="mailto:pabril@student.42.fr">pabril@student.42.fr</v-btn>
            </v-alert><br>

            <v-text-field
            outline
            required
            label="Your name"
            v-model="name"
            prepend-inner-icon="account_box"
          ></v-text-field>

          <v-text-field
            outline
            required
            name="_replyto"
            v-model="email"
            :rules="emailRules"
            label="Your email"
            prepend-inner-icon="mail"
          ></v-text-field>

          <v-textarea
          outline
          required
          name="text"
          label="Your message"
            prepend-inner-icon="message"
          :value="content"
        ></v-textarea>

        <v-btn class="success" type="submit">Let's send this mail !</v-btn>

        </v-container>
    </v-form>
</template>

<script>
export default {
    data() {
        return {
            name: "",
            email: "",
            content: "",

            emailRules: [
                v => !!v || 'E-mail is required',
                v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
            ]
        }
    }
}
</script>
